const getIcon = (string) => {
    if (string === 'LocationIcon') return LocationIcon
    if (string === 'PhoneIcon') return PhoneIcon
    if (string === 'WebsiteIcon') return WebsiteIcon
    if (string === 'TwitterIcon') return TwitterIcon
    if (string === 'GithubIcon') return GithubIcon
    if (string === 'OtherIcon') return OtherIcon
    if (string === 'EmailIcon') return EmailIcon
    if (string === 'LinkedinIcon') return LinkedinIcon
    if (string === 'InstagramIcon') return InstagramIcon

}
 
const RenderSvg = ({
    component: Component, 
    width,
    height,
    circleFill,
    pathFill
    }) => {
    return (
        <Component width={width} height={height} circleFill={circleFill} pathFill={pathFill}  />
    )
}


export {
    LocationIcon,
    InstagramIcon,
    WebsiteIcon,
    PhoneIcon,
    LinkedinIcon,
    EmailIcon,
    TwitterIcon,
    OtherIcon,
    GithubIcon,
    RenderSvg,
    getIcon
}

